<template>
  <div>
    <h1 class="display-3">{{cur_item["Name"]}}</h1>
    <img v-for="picture,a,b in cur_item['Images'].split(',')" :src="picture" :key="b" />

    <p v-for="value, field, i in cur_item" :key="i">{{field}}:  {{value}}</p>
  </div>
</template>

<script>
import data from "../../sample_shop_data.json";

// const item = data.find(e => e.Name = this.$route.params.item)

export default {
  data() {
    return {
      // cur_item: data.find(e => e.Name = this.$route.params.item)
    }
  },

  computed: {
    cur_item() {
      return data.find(e => e.SKU === this.$route.params.item)
    }
  },
  mounted() {
    console.log(this.$route.params.item)
    console.log("Current item: " + this.cur_item)
  }
};
</script>

